"use client";

import React, { useState } from "react";
import { useForm } from "react-hook-form";
import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import InfantList from "./infantList";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQueryClient } from "@tanstack/react-query";

// Shadcn UI components
import {
  Dialog,
  DialogTrigger,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

const InfantManagement = () => {
  const { createInfant } = useProtectedRoutesApi();
  const queryClient = useQueryClient();
  const [open, setOpen] = useState(false);

  // react-hook-form setup
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  const onSubmit = async (data) => {
    // Transform form data to match the API structure
    const infantPayload = {
      fullname: data.fullname,
      place_of_birth: data.place_of_birth,
      height: parseFloat(data.height),
      weight: parseFloat(data.weight),
      gender: data.gender,
      mothers_name: data.mothers_name,
      fathers_name: data.fathers_name,
      health_center: data.health_center,
      family_no: Number(data.family_no),
      image: data.image, // optional image url
      birthday: {
        month: Number(data.birthdayMonth),
        day: Number(data.birthdayDay),
        year: Number(data.birthdayYear),
      },
      address: {
        purok: data.purok,
        baranggay: data.baranggay,
        municipality: data.municipality,
        province: data.province,
      },
    };

    try {
      await createInfant(infantPayload);
      queryClient.invalidateQueries(["infants"]);
      reset();
      setOpen(false);
    } catch (error) {
      console.error("Error creating infant:", error);
    }
  };

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      <Sidebar />

      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col h-[calc(100vh-4rem)] space-y-8">
        {/* Container for the Add Infant button, positioned at the top right */}
        <div className="flex justify-end">
          <Dialog open={open} onOpenChange={setOpen}>
            <DialogTrigger asChild>
              <Button variant="default" onClick={() => setOpen(true)}>
                Add Infant
              </Button>
            </DialogTrigger>
            <DialogContent className="max-h-[80vh] overflow-y-auto">
              <DialogHeader>
                <DialogTitle>Add Infant</DialogTitle>
              </DialogHeader>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
                {/* Fullname */}
                <div className="flex flex-col">
                  <label htmlFor="fullname">Full Name</label>
                  <Input
                    id="fullname"
                    placeholder="Baby Name"
                    {...register("fullname", { required: true })}
                  />
                  {errors.fullname && (
                    <span className="text-red-500">This field is required</span>
                  )}
                </div>

                {/* Place of Birth */}
                <div className="flex flex-col">
                  <label htmlFor="place_of_birth">Place of Birth</label>
                  <Input
                    id="place_of_birth"
                    placeholder="City Hospital"
                    {...register("place_of_birth", { required: true })}
                  />
                </div>

                {/* Height */}
                <div className="flex flex-col">
                  <label htmlFor="height">Height</label>
                  <Input
                    id="height"
                    type="number"
                    step="0.1"
                    placeholder="50.5"
                    {...register("height", { required: true })}
                  />
                </div>

                {/* Weight */}
                <div className="flex flex-col">
                  <label htmlFor="weight">Weight</label>
                  <Input
                    id="weight"
                    type="number"
                    step="0.1"
                    placeholder="3.2"
                    {...register("weight", { required: true })}
                  />
                </div>

                {/* Gender */}
                <div className="flex flex-col">
                  <label htmlFor="gender">Gender</label>
                  <Input
                    id="gender"
                    placeholder="Male"
                    {...register("gender", { required: true })}
                  />
                </div>

                {/* Mother's Name */}
                <div className="flex flex-col">
                  <label htmlFor="mothers_name">Mother's Name</label>
                  <Input
                    id="mothers_name"
                    placeholder="Mother's Name"
                    {...register("mothers_name", { required: true })}
                  />
                </div>

                {/* Father's Name */}
                <div className="flex flex-col">
                  <label htmlFor="fathers_name">Father's Name</label>
                  <Input
                    id="fathers_name"
                    placeholder="Father's Name"
                    {...register("fathers_name", { required: true })}
                  />
                </div>

                {/* Health Center */}
                <div className="flex flex-col">
                  <label htmlFor="health_center">Health Center</label>
                  <Input
                    id="health_center"
                    placeholder="City Health Center"
                    {...register("health_center", { required: true })}
                  />
                </div>

                {/* Family Number */}
                <div className="flex flex-col">
                  <label htmlFor="family_no">Family No.</label>
                  <Input
                    id="family_no"
                    type="number"
                    placeholder="123"
                    {...register("family_no", { required: true })}
                  />
                </div>

                {/* Image URL */}
                <div className="flex flex-col">
                  <label htmlFor="image">Image URL (optional)</label>
                  <Input
                    id="image"
                    placeholder="optional-image-url"
                    {...register("image")}
                  />
                </div>

                {/* Birthday Fields */}
                <div className="grid grid-cols-3 gap-4">
                  <div className="flex flex-col">
                    <label htmlFor="birthdayMonth">Birthday Month</label>
                    <Input
                      id="birthdayMonth"
                      type="number"
                      placeholder="5"
                      {...register("birthdayMonth", { required: true })}
                    />
                  </div>
                  <div className="flex flex-col">
                    <label htmlFor="birthdayDay">Birthday Day</label>
                    <Input
                      id="birthdayDay"
                      type="number"
                      placeholder="15"
                      {...register("birthdayDay", { required: true })}
                    />
                  </div>
                  <div className="flex flex-col">
                    <label htmlFor="birthdayYear">Birthday Year</label>
                    <Input
                      id="birthdayYear"
                      type="number"
                      placeholder="2023"
                      {...register("birthdayYear", { required: true })}
                    />
                  </div>
                </div>

                {/* Address Fields */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex flex-col">
                    <label htmlFor="purok">Purok</label>
                    <Input
                      id="purok"
                      placeholder="3"
                      {...register("purok", { required: true })}
                    />
                  </div>
                  <div className="flex flex-col">
                    <label htmlFor="baranggay">Baranggay</label>
                    <Input
                      id="baranggay"
                      placeholder="Barangay Name"
                      {...register("baranggay", { required: true })}
                    />
                  </div>
                  <div className="flex flex-col">
                    <label htmlFor="municipality">Municipality</label>
                    <Input
                      id="municipality"
                      placeholder="Municipality"
                      {...register("municipality", { required: true })}
                    />
                  </div>
                  <div className="flex flex-col">
                    <label htmlFor="province">Province</label>
                    <Input
                      id="province"
                      placeholder="Province"
                      {...register("province", { required: true })}
                    />
                  </div>
                </div>

                <DialogFooter className="flex justify-end space-x-2">
                  <DialogClose asChild>
                    <Button variant="secondary" type="button">
                      Cancel
                    </Button>
                  </DialogClose>
                  <Button variant="default" type="submit">
                    Submit
                  </Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        </div>

        {/* Infant list display */}
        <InfantList />
      </main>

      <Footer />
    </div>
  );
};

export default InfantManagement;
